  margin-top: 1em;
  border: 2px solid #ececec;
  border-top-width: 1px;
  border-radius: 2px; }
  .reservation-details p {
    margin-top: 0;
    padding: 10px 20px 0;
    border-top: 1px solid #ececec; }
  .reservation-details strong {
    color: #545454;
    font-size: 16px;
    font-weight: 400;
    line-height: 1.2em;
    display: block;
    padding-top: 5px;
    padding-bottom: 3px; }
  .reservation-details .night-stay > strong {
    color: #1885dc; }
  .reservation-details .abrow > [class*="abcol-"]:first-child {
    border-right: solid 1px #ececec; }

table.wp-list-table .column-rate {
  width: 10%; }

table.wp-list-table .column-rooms {
  width: 15%; }

table.wp-list-table .column-capacity {
  width: 20%; }

table.wp-list-table .column-thumb {
  width: 52px;
  text-align: center; }
  @media only screen and (max-width: 782px) {
    table.wp-list-table .column-thumb {
      display: none;
      text-align: left; }
      table.wp-list-table .column-thumb:before {
        content: '' !important; } }
  table.wp-list-table .column-thumb > a {
    overflow: hidden;
    display: inline-block;
    vertical-align: middle;
    border-radius: 50%;
    width: 38px;
    height: 38px; }
  table.wp-list-table .column-thumb > a > img {
    width: 100%;
    height: auto; }

.wrap.awebooking-settings .subsubsub {
  float: none;
  margin-bottom: 8px; }
  .wrap.awebooking-settings .subsubsub > li:not(:last-child):after {
    content: '|';
    color: #aaa;
    margin: 0 5px;
    display: inline-block; }

.wrap.awebooking-settings .cmb-th {
  position: relative; }

.wrap.awebooking-settings .cmb2-tooltip {
  top: 0;
  right: 0;
  cursor: help;
  position: absolute;
  color: #585858; }
  .wrap.awebooking-settings .cmb2-tooltip .dashicons {
    font-size: 18px; }

.wrap.awebooking-settings select.cmb2_select,
.wrap.awebooking-settings input.regular-text,
.wrap.awebooking-settings .selectize-control {
  width: 400px; }

.wrap.awebooking-settings p.cmb2-metabox-description,
.wrap.awebooking-settings span.cmb2-metabox-description {
  color: #666;
  padding-top: 5px;
  font-style: inherit; }
